import { getStringDate } from "./../../features/dates";
import create from "zustand";
import moment from "moment";

import { ICompany, ICompanyFull } from "entities/company/types";
import { IMember } from "entities/member/types";
import { IMeeting, IQuestion } from "entities/meeting/types";
import { API_COMPANIES } from "pages/companies/api-companies";
import { API_COMPANY } from "pages/companyDetails/api-company";
import { API_MEETING } from "pages/meetingDetails/api-meeting";
import { API_QUESTIONS } from "pages/meetingDetails/blocks/questionsBlock/api-questions";
import { IAnswer } from "entities/answer/types";
import { API_ANSWERS } from "pages/meetingDetails/blocks/answersBlock/api-answers";
import { dateWithTimeFormat } from "features/dates";
import { API_CATALOGS } from "shared/api/api-catalogs";

interface CompanyState {
  companies: ICompany[];
  getCompanies: () => void;
  addCompany: (newCompany: ICompany) => void;
  company: ICompanyFull;
  getCompany: (id: string) => void;
  addMember: (data: IMember) => void;
  changeMember: (data: IMember) => void;
  meeting: IMeeting;
  getMeeting: (id: string) => void;
  addMeeting: (company_id: string, name: string) => void;
  changeMeetingInfo: (meetingInfo: any) => void;
  changeMeetingFactInfo: (meetingInfo: any) => void;
  addQuestion: (data: IQuestion) => void;
  changeQuestion: (data: IQuestion) => void;
  addAnswer: (data: IAnswer[]) => void;
  changeAnswer: (data: IAnswer[]) => void;
  catalogs: any;
  getGatalog: (type: string) => void;
}

export const useStore = create<CompanyState>()((set) => ({
  companies: [],
  company: {
    id: "",
    requisites: { name: "", inn: "", kpp: "", address: "" },
    members: [],
    meetings: [],
  },
  getCompanies: async () => {
    const response = await API_COMPANIES.getAll();
    set({ companies: response });
  },
  addCompany: async (newCompany) => {
    const response = await API_COMPANIES.add(newCompany);
    set((state) => ({ companies: [...state.companies, response] }));
  },
  getCompany: async (id: string) => {
    const response = await API_COMPANY.get(id);
    set({ company: response });
  },
  addMember: async (data: IMember) => {
    const response = await API_COMPANY.addMember(data);
    set((state) => ({
      company: { ...state.company, members: [...state.company.members, data] },
    }));
    // set((state) => ({ company: {...state.company, members: [...state.company?.members, response]} }));
  },
  changeMember: async (data: IMember) => {
    const response = await API_COMPANY.changeMember(data);
    set((state) => ({
      company: {
        ...state.company,
        members: state.company.members.map((member) =>
          member.id === data.id ? { ...member, ...data } : member
        ),
      },
    }));
  },
  meeting: {
    id: "",
    name: "",
    info: { name: "" },
    // typeOfÐ¡ause: '',
    questions: [],
    answers: [],
  },
  getMeeting: async (id: string) => {
    const response = await API_MEETING.get(id);
    console.log(
      "ðŸš€ ~ file: index.ts ~ line 72 ~ getMeeting: ~ response",
      response
    );
    set({
      meeting: {
        ...response,
        info: {
          ...response.info,
          date_of_meeting:
            response.info.date_of_meeting &&
            moment(response.info.date_of_meeting, dateWithTimeFormat),
          date_of_registration:
            response.info.date_of_registration &&
            moment(response.info.date_of_registration, dateWithTimeFormat),
          date_of_start:
            response.info.date_of_start &&
            moment(response.info.date_of_start, dateWithTimeFormat),
          date_of_finish:
            response.info.date_of_finish &&
            moment(response.info.date_of_finish, dateWithTimeFormat),
        },
      },
    });
    // set({ meeting: response});
  },
  addMeeting: async (company_id: string, name: string) => {
    const response = await API_MEETING.add(company_id, name);
    set((state) => ({
      company: {
        ...state.company,
        meetings: [...state.company.meetings, response],
      },
    }));
  },
  changeMeetingInfo: async (meetingInfo: any) => {
    const meetingInfoWithStringDates = {
      ...meetingInfo,
      date_of_meeting: getStringDate(meetingInfo.date_of_meeting),
      date_of_registration: getStringDate(meetingInfo.date_of_registration),
    };
    console.log(
      "ðŸš€ ~ file: index.ts ~ line 96 ~ changeMeetingInfo: ~ meetingInfo",
      meetingInfoWithStringDates
    );
    const response = await API_MEETING.changeInfo(meetingInfoWithStringDates);
    set((state) => ({
      meeting: {
        ...state.meeting,
        info: { ...state.meeting.info, meetingInfo },
      },
    }));
  },
  changeMeetingFactInfo: async (meetingInfo: any) => {
    const meetingInfoWithStringDates = {
      ...meetingInfo,
      date_of_start: getStringDate(meetingInfo.date_of_start),
      date_of_finish: getStringDate(meetingInfo.date_of_finish),
    };
    console.log(
      "ðŸš€ ~ file: index.ts ~ line 96 ~ changeMeetingInfo: ~ meetingInfo",
      meetingInfoWithStringDates
    );
    const response = await API_MEETING.changeFactInfo(
      meetingInfoWithStringDates
    );
    set((state) => ({
      meeting: {
        ...state.meeting,
        info: { ...state.meeting.info, meetingInfo },
      },
    }));
  },
  addQuestion: async (data: IQuestion) => {
    const response = await API_QUESTIONS.add(data);
    set((state) => ({
      meeting: {
        ...state.meeting,
        questions: [...state.meeting.questions, response],
      },
    }));
  },
  changeQuestion: async (data: IQuestion) => {
    const response = await API_QUESTIONS.change(data);
    set((state) => ({
      meeting: {
        ...state.meeting,
        questions: state.meeting.questions.map((question) =>
        question.id === data.id ? { ...question, ...data } : question
        ),
      },
    }));
  },
  addAnswer: async (data: IAnswer[]) => {
    const response = await API_ANSWERS.add(data);
    set((state) => ({
      meeting: {
        ...state.meeting,
        answers: [...state.meeting.answers, ...response],
      },
    }));
  },
  changeAnswer: async (data: IAnswer[]) => {
    console.log("ðŸš€ ~ file: index.ts ~ line 189 ~ changeAnswer: ~ data", data)
    const response = await API_ANSWERS.change(data);
    // set((state) => {
    //   // const newAnswers = state.meeting.answers.map(answer => answer.question_id === )
    //   console.log(state.meeting.answers)
    //   return {

    //   }
    // })
    // set((state) => ({
    //   meeting: {
    //     ...state.meeting,
    //     answers: [...state.meeting.answers, ...response],
    //   },
    // }));
  },
  catalogs: {},
  getGatalog: async (type: string) => {
    const response = await API_CATALOGS.get(type);
    set((state) => ({
      catalogs: {
        ...state.catalogs,
        [type]: response,
      },
    }));
  },
}));
